@use 'base/variables' as v;

/************ Banner Css ************/
.banner {
  margin-top: 0;
  // padding: 260px 0 100px 0;
  max-height: 70rem;
  min-height: 60rem;
  position:relative;
}
.overlay {
  position: absolute;
  background: linear-gradient(270deg, v.$negro, #3AA58c55 100%);
  width: 100%;
  height: 100%;
  // bottom:20%;
}
.background{
  position: absolute;
  width: 100%;
  height: 100%;
}
.banner-content{
  padding: 260px 0 0 0;
  z-index: 10;
  
  // background: linear-gradient(270deg, v.$negro, #3AA58c55 100%);
  // width: 100%;
  // height: 100%;
}



.banner .tagline {
  font-weight: 700;
  letter-spacing: 0.8px;
  padding: 8px 10px;
  background: linear-gradient(90.21deg, v.$fondo1 -5.91%, v.$fondo4 111.58%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  font-size: 20px;
  margin-bottom: 16px;
  display: inline-block;
}

.banner h1 {
  font-size: 65px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1;
  margin-bottom: 20px;
  display: block;
}

.banner p {
  color: v.$blanco;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
}

.banner button {
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  margin-top: 60px;
  letter-spacing: 0.8px;
  display: flex;
  align-items: center;
}

.banner button svg {
  font-size: 25px;
  margin-left: 10px;
  transition: 0.3s ease-in-out;
  line-height: 1;
}

.banner button:hover svg {
  margin-left: 25px;
}

.banner-content img {
  animation: updown 3s linear infinite;
}

@keyframes updown {
  0% {
    transform: translateY(-20px);
  }

  50% {
    transform: translateY(20px);
  }

  100% {
    transform: translateY(-20px);
  }
}

.txt-rotate>.wrap {
  border-right: 0.08em solid #666;
}